header {
    height: 80px;
    background: #11101b;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 100px;
    position: sticky;
    top: 0;
  }
  
  .logo {
    font-size: 28px;
    font-weight: bold;
    color: white;
  }
  
  .burger {
    display: flex;
  }
  .nav-bar ul {
    display: flex;
    list-style: none;
    column-gap: 10px;
  }
  .nav-bar ul li .Link {
    display: block;
    color: white;
    font-size: 20px;
    border-radius: 50px;
    transition: 0.4s all;
    text-decoration: none;
    margin: 0 5px;
    padding: 5px;
  }
  
  .nav-bar ul li .Link:hover{
    background-color: white;
    color: black;
  }
  
  .Link{
    text-decoration: none;
    color: white;
  }
  
  @media only screen and (max-width: 1320px){
      header{
          padding: 0 50px;
      }
  }
  
  @media only screen and (max-width: 1100px){
      header{
          padding: 0 30px;
      }
  }
  
  @media only screen and (max-width: 900px){
      .burger{
          display: block;
          cursor: pointer;
      }
      .burger .line{
          width: 30px;
          height: 3px;
          background: white;
          margin: 6px 0;
      }
      .nav-bar{
        position: absolute;
        top: 80px;
        left: 0;
        right: 0;
        background-color: #11101b;
        transition: 0.3s;
        height: 0;
        overflow: hidden;
      }
      .nav-bar.active{
        height: 400px;
      }
  
      .nav-bar.active ul{
        opacity: 1;
      }
  
      .nav-bar ul{
        display: block;
        width: fit-content;
        margin: 80px auto 0 auto;
        opacity: 0;
        text-align: center;
      }
      .nav-bar ul li .Link{
        margin-bottom: 12px;
  
      }
  }